import React from 'react';
import './Card.css';

const Card = ({ GameTitle, GameDescription, GameImage }) => {
  return (
    <div className = 'Card'>
      <img alt='Oops!' src = { GameImage } />
      <p>Title { GameTitle }</p>
      <p>Description: { GameDescription }</p>
    </div>);
}

export default Card;
