import React from 'react';
import Card from './Card';
import './Card.css';

const CardList = ({ CatalogList }) => {
  return(
    <div className='container'>
    {
      CatalogList.map((user, i) => {
        return(
          <Card
            key={i}
            GameTitle={CatalogList[i].GameTitle}
            GameDescription={CatalogList[i].GameDescription}
            Topic={CatalogList[i].Topic}
            Group={CatalogList[i].Group}
            Level={CatalogList[i].Level}
            Subject={CatalogList[i].Subject}
            GameImage={CatalogList[i].GameImage}
            />
          );
        }
      )}
    </div>
  );

}

export default CardList;
